<template>
	<div id="goods-search">
		<public-header title="商品搜索"></public-header>
		<div class="content">
			<div class='search-header van-hairline--bottom'>
				<div class="search-content">
					<van-icon name="search"/>
					<input type="text" class="search-input" placeholder="请输入商家或商品名称">
				</div>
				<div class="search-btn">搜索</div>
			</div>
			<div class='rmd-history'>
				<div class='search-rmd-wrap'>
					<div class='search-rmd-title '>
						<div class="van-hairline--bottom">热门搜索</div>
					</div>
					<div class='search-rmd'>
<!--
						<router-link to="" tag="div" v-for="item in 6">热门商品</router-link>
-->
					</div>
				</div>
			</div>
			<div class="goods-search-warp">
				<div class="goods-list">
					<div class="goods-item van-hairline--bottom flex-lr">
						<div class="goods-thumb">
							<img class="img-100" src="https://fuss10.elemecdn.com/e/46/02b0c5d2fdfca3e11a13ad3fa4094jpeg.jpeg" alt="">
						</div>
						<div class="goods-info">
							<div class="w-100 ellipsis font-14 font-500">商品名称商品名称商品名称商品名称商品名称商品名称商品名称商品名称</div>
							<div class="c-gray flex font-12">
								<span>月售100</span>
								<span class="margin-15-l">赞100</span>
							</div>
							<div class="font-500 c-danger font-14">¥10.00/斤</div>
							<div class="goods-operation">
								<goods-handle
									:goods="item"
									:optionId="0"
									from="cart"
								>
								</goods-handle>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import GoodsHandle from '@/components/goodsHandle'
	import {mapState, mapMutations} from 'vuex'
	export default {
		data() {
			return {
				item: {
					attrs: [],
					box_price: "0",
					child_id: "0",
					cid: "1254",
					comment_good: "1",
					content: "",
					end_time1: "",
					end_time2: "",
					id: "9766",
					is_attrs: 0,
					is_options: "0",
					is_sail_now: 1,
					is_showtime: "0",
					label: "",
					options: [],
					options_data: [{id: "0", name: "", price: "10", total: "9963", num: 1}],
					price: "10",
					sailed: "20",
					start_time1: "",
					start_time2: "",
					status: "1",
					thumb: "https://fuss10.elemecdn.com/e/78/215f359d53415b5edb10046f0092cjpeg.jpeg",
					time_cn: "",
					title: "铁板牛肉煎饺",
					total: "9963",
					totalnum: 1,
					ts_price: "8",
					unitname: "份",
					unitname_cn: "/份",
					week: "1,2,3",
					week_cn: ""
				}
			}
		},
		components: {
			PublicHeader,
			GoodsHandle
		},
		methods: {

		},
		computed: {
			...mapState([
				'istore',
			]),

		},
	}
</script>

<style>
	#goods-search .search-header{
		background-color: #fff;
		width: 100%;
		height: 50px;
		box-sizing: border-box;
		padding:10px;
		display:flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
	}
	#goods-search .search-header .search-content{
		width: 90%;
		height: 100%;
		border-radius: 40px;
		position: relative;
	}
	#goods-search .search-header .search-content .van-icon{
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		width: 30px;
		line-height: 28px;
		text-align: center;
		font-size: 14px;
	}
	#goods-search .search-header .search-content .search-input{
		width: 100%;
		height: 100%;
		line-height: 28px;
		border-radius: 40px;
		padding-left: 25px;
		box-sizing: border-box;
		background: #f2f2f2;
		font-size: 14px;
	}
	#goods-search .search-header .search-btn{
		font-size: 14px;
		color: #FF2D4B;
	}
	#goods-search .rmd-history{
		margin-bottom: 10px;
		background-color: #f5f5f5;
	}
	#goods-search .rmd-history .search-rmd-title{
		position: relative;
		padding-left: 15px;
		font-size: 15px;
		line-height: 38px;
		color: #656565;
		background: #fff;
	}
	#goods-search .rmd-history .search-rmd{
		padding: 10px;
		background: #fff;
		overflow: hidden;
	}
	#goods-search .rmd-history .search-rmd div{
		display: inline-block;
		float: left;
		max-width: 187px;
		min-width: 58px;
		height: 31px;
		line-height: 31px;
		margin: 5px;
		padding: 0 16px;
		-webkit-box-sizing: border-box;
		-mox-box-sizing: border-box;
		box-sizing: border-box;
		-webkit-border-radius: 2px;
		-moz-border-radius: 2px;
		border-radius: 2px;
		border: 1px solid #ccc;
		color: #2f2f2f;
		overflow: hidden;
		white-space: nowrap;
		text-overflow: ellipsis;
		text-align: center;
		font-size: 14px;
	}
	#goods-search .goods-search-warp{
		margin-bottom: 10px;
		background-color: #fff;
	}
	#goods-search .goods-search-warp .goods-list .goods-item{
		position: relative;
		padding: 10px 15px;
	}
	#goods-search .goods-item .goods-thumb{
		width: 56px;
		height: 56px;
		font-size: 0px;
		border-radius: 3px;
		overflow: hidden;
	}
	#goods-search .goods-item .goods-info{
		max-width: 80%;
		height: 56px;
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;
		justify-content: space-between;
		position: relative;
	}
	#goods-search .goods-item .goods-operation{
		position: absolute;
		bottom: 0;
		right: 0;
		min-width: 100px;
	}
	#goods-search .goods-item .goods-operation .foodop{
		right: 0;
		bottom: 0;
	}
</style>


